<template>
  <div>
    <van-nav-bar title="登录" left-arrow @click-left="$router.back()"></van-nav-bar>
    <van-cell-group>
      <van-field required @blur="checkMobile" :error-message="mobile_error" v-model="loginForm.mobile" placeholder="请输入手机号" label="手机号"/>
      <van-field required @blur="checkCode" :error-message="code_error" v-model="loginForm.code" placeholder="请输入验证码" label="验证码" >
        <div slot="button">
          <van-button type="primary" size="small">发送验证码</van-button>
        </div>
      </van-field>
    </van-cell-group>
    <div class="btn-login">
      <van-button type="info" block round size="small" @click="login">登录</van-button>
    </div>
  </div>
</template>

<script>
import store from '@/store'
import { login } from '@/api/user.js'
export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        mobile: '13911111111',
        code: '246810'
      },
      mobile_error: '',
      code_error: ''
    }
  },
  methods: {
    checkMobile () {
      if (!this.loginForm.mobile) {
        // 如果手机号是空
        this.mobile_error = '手机号不能为空'
        return false
      } else if (!/^1[3-9]\d{9}$/.test(this.loginForm.mobile)) {
        this.mobile_error = '手机号格式不正确'
        return false
      } else {
        this.mobile_error = ''
        return true
      }
    },
    checkCode () {
      if (!this.loginForm.code) {
        // 如果验证码是空
        this.code_error = '验证码不能为空'
        return false
      } else if (!/^\d{6}$/.test(this.loginForm.code)) {
        this.code_error = '验证码格式不正确'
        return false
      } else {
        this.code_error = ''
        return true
      }
    },
    async login () {
      // 校验表单
      if (this.checkMobile() && this.checkCode()) {
        // 验证都通过了
        // 先去发送请求
        const data = await login(this.loginForm)
        // 得到的是token和refresh_token
        // 把token存起来

        store.commit('updateUser', { user: data })
        // 弹出一个提示框
        this.$gnotify({ type: 'success', message: '登录成功' })
        // 存好以后判断是否有传参
        if (this.$route.query.redirectUrl) {
          // 如果有传参的话
          // 要跳转回原来的页面
          this.$router.push(this.$route.query.redirectUrl)
        } else {
          // 如果没有传参的话就跳回到首页
          this.$router.push('/')
        }
      }
    }
  }

}
</script>

<style lang="less" scoped>
.btn-login{
  padding:20px;
}
</style>
